export let volume = {
    repsPercent : {
        "1" : 100/100,
        "2" : 95/100,
        "3" : 87.5/100,
        "4" : 85/100,
        "5" : 80/100,
        "6" : 77.5/100,
        "7" : 75/100,
        "8" : 75/100,
        "9" : 72.5/100,
        "10" : 70/100,
        "11" : 70/100,
        "12" : 65/100,
        "13" : 65/100,
        "14" : 65/100,
        "15" : 60/100,
        "16" : 60/100,
        "17" : 55/100,
        "18" : 55/100,
        "19" : 55/100,
        "20" : 50/100,
        ">20" : 40/100,
    },
    
    getVolume : function( reps ) {

        if (!this.repsPercent[reps]) {
            return false;
        }

        return this.repsPercent[reps];
    }
}